import Head from "next/head";
import Header from "../components/header";

const HeadContents = () => (
  <div>
    <title>Rent A Roof</title>
    <link
      rel="stylesheet"
      href="https://stackpath.bootstrapcdn.com/bootstrap/4.2.1/css/bootstrap.min.css"
    />
    <link
      rel="stylesheet"
      href="https://use.fontawesome.com/releases/v5.6.3/css/all.css"
      integrity="sha384-UHRtZLI+pbxtHCWp1t77Bi1L4ZtiqrqD80Kn4Z8NTSRyMA2Fd33n5dQ8lWUE00s/"
      crossorigin="anonymous"
    />
    <link rel="stylesheet" href="../static/css/index.css" />
  </div>
);

export default () => (
  <div>
    <Head>
      <HeadContents />
    </Head>
    <Header />
    <div id="landing-bg">
      <div id="landing-text">
        <h1 id="landing-heading">A home for everyone</h1>
        <br />
        <br />
        <input id="suburb-input" placeholder="Type Suburb" />
      </div>
    </div>

    {/* we can map all of these locations actually from the data store */}
    <div id="provinces">
      <h1>Property by provinces</h1>
    </div>
  </div>
);
